.usersModal .title, .usersModal .item {
  font-family: 'Jura'; }

.usersModal .title {
  font-size: 26px;
  font-weight: bold; }

.usersModal .item {
  font-size: 20px; }

.usersModal .animate-item.ng-enter {
  animation: fadeInLeft 1s; }

.usersModal .animate-item.ng-enter-stagger {
  animation-delay: 200ms;
  animation-duration: 0; }
